.intro {
  height: 995px;
  display: flex;
  align-items: center;
  background-color: #F1FCF7;
  position: relative;
  overflow: hidden;
  background-image: url('../img/intro/pic.png');
  background-repeat: no-repeat;
  background-position: 100% 0;
  background-size: auto 100%;
  padding-bottom: 130px;
  @include r(992) {
    height: auto;
    padding-top: 110px;
    padding-bottom: 70px;
    display: block;
    &::before {
      content: '';
      background: #F1FCF7;
      opacity: 0.8;
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0; } }
  &__pastor {
    position: absolute;
    left: 50%;
    bottom: -4px;
    width: 475px;
    @include r(992) {
      position: relative;
      left: 0;
      top: 0;
      padding-top: 70px;
      width: 100%;
      z-index: 2; }
    img {
      width: 100%;
      position: relative;
      z-index: 3;
      @include r(1140) {
        display: none; } }
    &__info {
      position: absolute;
      top: 40px;
      right: -80px;
      background: #FFFFFF;
      box-shadow: 0px 5px 30px rgba(198, 207, 217, 0.3);
      border-radius: 8px;
      width: 415px;
      padding: 20px 65px;
      z-index: 2;
      @include r(992) {
        position: relative;
        left: 0;
        top: 0;
        padding: 20px 35px;
        width: 100%; }
      p {
        font-weight: bold;
        font-size: 16px;
        line-height: 24px;
        color: #050606;
        margin-bottom: 12px; }
      span {
        font-weight: normal;
        font-size: 36px;
        line-height: 42px;
        color: #050606;
        span {
          color: #53BF51;
          font-weight: bold;
          margin-left: 10px; } } } }
  &__text {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
    z-index: 7;
    @include r(992) {
      padding-bottom: 20px;
      height: auto; }
    h1 {
      font-weight: bold;
      font-size: 64px;
      line-height: 1.3;
      margin-bottom: 30px;
      @include r(767) {
        font-size: 36px; } }
    p {
      font-weight: 500;
      font-size: 16px;
      line-height: 24px;
      max-width: 445px;
      width: 100%;
      color: #8D8D8C;
      margin-bottom: 60px;
      @include r(767) {
        margin-bottom: 0px; } }
    a {
      font-weight: bold;
      font-size: 16px;
      display: flex;
      align-items: center;
      color: #53BF51;
      @include r(1200) {
        margin: 30px 0; }
      i.icon-play {
        margin-right: 25px;
        @include tr;
        background: linear-gradient(95.5deg, #53BF51 0%, #8BDC8A 100%);
        box-shadow: 0px 5px 20px rgba(0, 177, 0, 0.5);
        animation: pulse2 1.5s infinite;
        &::before {
          border-left: 12px solid #fff; } }
      &:hover {
        text-decoration: underline;
        i {
          transform: scale(1.1);
          box-shadow: 0px 10px 20px rgba(198, 207, 217, 0.3); } } } }

  &__num {
    background: #F1FCF7;
    border: 1px dashed #53BF51;
    width: 450px;
    padding: 30px 50px;
    position: relative;
    &::before {
      content: '';
      width: 1px;
      height: 200px;
      border-right: 1px dashed #53BF51;
      position: absolute;
      bottom: -200px;
      left: 50%;
      @include r(992) {
        height: 70px;
        bottom: -70px; } }
    @include r(992) {
      width: 100%;
      z-index: 3;
      padding: 8px 20px; }
    @include r(420) {
      padding: 8px 12px; }
    strong {
      color: #050606;
      font-weight: bold;
      font-size: 16px;
      line-height: 24px;
      display: block;
      text-align: center;
      margin-bottom: 15px;
      @include r(420) {
        font-size: 14px; } }
    span {
      font-weight: bold;
      font-size: 48px;
      display: flex;
      justify-content: center;
      color: #53BF51;
      @include r(420) {
        font-size: 32px; }
      span {
        margin-right: 12px;
        display: block;
        color: #000;
        font-weight: 500; } } } }




@keyframes pulse2 {
  0% {
    box-shadow: 0 0 0 0 rgba(0, 177, 0, 0.5); }
  70% {
    box-shadow: 0 0 0 15px rgba(0, 177, 0, 0); }
  100% {
    box-shadow: 0 0 0 0 rgba(0, 177, 0, 0); } }
