.tabs {
  &__caption {
    display: flex;
    li {
      padding: 13px 40px;
      display: block;
      font-weight: bold;
      font-size: 16px;
      color: #050606;
      border-radius: 8px 8px 0px 0px;
      cursor: pointer;
      text-align: center;
      @include tr;
      @include r(767) {
        padding: 13px 31px; }
      @include r(445) {
        padding: 13px 16px; }
      @include r(374) {
        padding: 13px 10px; }
      &:hover {
        color: #53BF51; }
      &.active {
        background: #53BF51;
        color: #fff; }
      span {
        background: #FFC700;
        border-radius: 2px;
        padding: 4px;
        padding-right: 15px;
        font-weight: bold;
        font-size: 12px;
        display: inline-block;
        margin-left: 10px;
        position: relative;
        @include r(767) {
          color: transparent;
          width: 14px;
          height: 14px;
          overflow: hidden;
          padding: 0; }
        &::before {
          content: '';
          width: 6px;
          height: 6px;
          border-radius: 50%;
          position: absolute;
          right: 5px;
          top: 8px;
          background: #fff;
          animation: pulse3 1.5s infinite;
          @include r(767) {
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%); } } } } }
  &__content {
    display: none;
    height: 440px;
    overflow: auto;
    background: rgba(255, 255, 255, 0.8);
    backdrop-filter: blur(8px);
    &.active {
      display: block; } }
  &__table {
    tr {
      border-bottom: 1px solid #E0E0E0;
      align-items: center;
      display: flex;
      justify-content: space-between;
      padding: 15px 20px;
      @include r(767) {
        flex-direction: column; } }
    &__date {
      @include center; }
    td {
      font-size: 16px;
      color: #050606;
      line-height: 24px;
      display: flex;
      width: 100%;
      padding: 0 5px;
      justify-content: space-between;
      b {
        font-weight: bold;
        color: #53BF51;
        width: 70px;
        display: inline-block;
        @include r(767) {
          padding-top: 10px; } }
      span {
        color: #050606;
        margin-left: 30px;
        font-weight: 500;
        flex: 1;
        @include r(767) {
          padding-top: 10px; } } } } }


@keyframes pulse3 {
  0% {
    opacity: 1; }
  50% {
    opacity: 0; }
  100% {
    opacity: 1; } }
